import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import {faUserAstronaut, faGlobe, faGamepad, faPalette, faFireExtinguisher, faFireAlt, faUsers, faRocket, faMusic, faCompactDisc, faBezierCurve, faHandPeace, faHeadphonesAlt, faFilm, faCalendarCheck } from '@fortawesome/free-solid-svg-icons';
import WorkplaceIcon from '../../assets/icons/workplace.svg';
import RemoteIcon from '../../assets/icons/remote.svg';
import SlideDown from '../UI/SlideDown/SlideDown';

import './WorkingIdentity.scss';

const WorkingIdentity = (props) => (
  <div className="wi">
  <h2 className="wi-subtitle"><span className="wi-subtitle-icon"><FontAwesomeIcon icon={faUserAstronaut} /></span><span style={{marginLeft: "6px"}}>работе:</span></h2>
    <SlideDown >
      <p className="wi-text"> Предпочел бы заниматься проектами, которые объединяют web и искусство <FontAwesomeIcon className="accent-color" icon={faPalette} /><span className="accent-color"> / </span>    
        используют геймификацию <FontAwesomeIcon className="accent-color" icon={faGamepad} /><span className="accent-color"> / </span>
        являются социально значимыми <FontAwesomeIcon className="accent-color" icon={faGlobe} />
    </p>
    <p className="wi-text">
        Приятно работать в уютном офисе и иметь личное пространство на рабочем месте <object height='20' data={WorkplaceIcon} > </object>
        <br/>Большой плюс для меня - возможность работать удаленно <object height='20' data={RemoteIcon} > </object>
        <br/>Морально готов справляться с авралами и форс-мажорами <FontAwesomeIcon className="accent-color" icon={faFireExtinguisher} /> 
        <FontAwesomeIcon className="accent-color" icon={faFireAlt} />. И хоть с ними жизнь ярче, работать лучше по плану и в спокойной обстановке <FontAwesomeIcon className="accent-color" icon={faCalendarCheck} />
    </p>
    <p className="wi-text">И важнее всего ни где и  над чем работать,  а с кем <FontAwesomeIcon className="accent-color" icon={faUsers} />
    <br/> Работа в команде единомышленников, в которой царят взаимопонимание и заинтересованность - продуктивнее и успешнее <FontAwesomeIcon className="accent-color" icon={faRocket} />
    </p>
    </SlideDown>
    <h2 className="wi-subtitle"><span className="wi-subtitle-icon"><FontAwesomeIcon icon={faHandPeace} /></span>б отдыхе:</h2>
    <SlideDown >
    <p className="wi-text">
       Мои хобби: написание музыки <FontAwesomeIcon className="accent-color" icon={faMusic} />, 
       диджеинг <FontAwesomeIcon className="accent-color" icon={faCompactDisc} />, 
       машинное обучение <FontAwesomeIcon className="accent-color" icon={faBezierCurve} />
    </p> 
    <p className="wi-text">
       Мои развлечения: 
       музыка <FontAwesomeIcon className="accent-color" icon={faHeadphonesAlt} />, 
       фильмы <FontAwesomeIcon className="accent-color" icon={faFilm} />, 
       игры <FontAwesomeIcon className="accent-color" icon={faGamepad} />
    </p>
    </SlideDown>
    <h2 className="wi-subtitle"><span className="wi-subtitle-icon"><FontAwesomeIcon icon={faGlobe} /></span><span style={{marginLeft: "6px"}}>сайте:</span></h2>
    <SlideDown >
    <p className="wi-text">
     Этот сайт - в первую очередь моя площадка для экспериментов и творчества, а заодно и резюме.
    </p> 
    <p className="wi-text">
      Буду рад комментариям по поводу всего, что вы здесь увидите:)
    </p>
    <p className="wi-text">
      Когда-нибудь тут будет блог, в котором я хочу делиться интересностями, которые узнаю, и записывать полезности, которые я могу забыть:)
    </p>
    <p className="wi-text">
      Спасибо за внимание!
    </p>
    </SlideDown>
  </div>
);

export default WorkingIdentity;